import { initWidget } from "./widget";
console.log("[widget-starter] Initializing widget using initWidget.");

initWidget(
  (widget) => {
    console.log(
      "[widget-starter] initWidget callback invoked. Widget:",
      widget
    );
    import("../index")
      .then((module) => {
        console.log("[widget-starter] Successfully imported ../index.");
        module.default();
      })
      .catch((error) => {
        console.error(
          "[widget-starter] Error dynamically importing ../index:",
          error
        );
        // Handle the error appropriately, e.g., display an error message to the user:
        widget.body.innerHTML =
          "<div style='color: red;'>Error loading widget content.</div>";
      });
  },
  (error) => {
    console.error("[widget-starter] initWidget encountered an error:", error);
  }
);
